fun readSource(sourceTxt: String): MutableList<Thirukkural> {
    val thirukurals = mutableListOf<Thirukkural>()
    val lines = sourceTxt.lines()
    for (line in lines) {
        if (line.isNotBlank()) {
            val values = line.split("|")
            val athikaramNo = values[0].toInt()
            val athikaram = values[1]
            val kuralNo = values[2].toInt()
            val kural = values[3]
            val porul = values[4]
            val words = getWords(kural)
            if (words.size != 7) println("No: $kuralNo, Words: $words")
            val kuralOnly = KuralOnly(
                words.take(4).joinToString(separator = " "),
                words.takeLast(3).joinToString(separator = " "))
            val thirukkural = Thirukkural(
                athikaramNo,
                athikaram,
                kuralNo,
                kuralOnly,
                porul,
                words)
            thirukurals.add(thirukkural)
        }
    }
    return thirukurals
}

fun getWords(line: String): List<String> {
    val list = mutableListOf<String>()
    var currentWord = ""
    val charArray = line.toCharArray()
    for (char in charArray) {
        when (char.toInt()) {
            // Space, Dot, Zero-width non-joiner
            32, 39, 44, 46, 58, 8204 -> {
                if (currentWord.isNotBlank()) {
                    list.add(currentWord)
                }
                currentWord = ""
            }
            else -> {
                currentWord += char
            }
        }
    }
    if (currentWord.isNotBlank()) list.add(currentWord)
    return list
}
